[data-tips] {
  position: relative;
  display: inline-block;
  cursor: pointer;
}
[data-tips]:hover:before {
  content: attr(data-tips);
  position: absolute;
  z-index: 999;
  top: -100%;
  left: 50%;
  margin-left: -60%;
  margin-top: -25px;
  display: block;
  min-width: 100%;
  padding: 5px 10px;
  pointer-events: none;
  white-space: nowrap;
  font-size: 14px;
  color: #fff;
  background: rgba(0, 0, 0, 0.8);
  border-radius: 5px;
  -webkit-transition: all ease-in-out 1s;
  -moz-transition: all ease-in-out 1s;
  transition: all ease-in-out 1s;
}
[data-tips]:hover:after {
  content: "";
  position: absolute;
  top: -15px;
  left: 50%;
  margin-left: -7px;
  display: block;
  width: 0;
  height: 0;
  border-left: 7px solid transparent;
  border-top: 10px solid rgba(0, 0, 0, 0.8);
  border-right: 7px solid transparent;
}
.vui-checkbox-warp {
  margin-right: 5px;
  -webkit-user-select: none;
