  font-size: 14px;
}
.vui-checkbox-warp .vui-checkbox {
  position: relative;
  height: 20px;
  padding-left: 22px;
}
.vui-checkbox-warp .vui-checkbox:before {
  content: ' ';
  position: absolute;
  left: 0;
  top: 0;
  z-index: 1;
  width: 16px;
  height: 16px;
  border-radius: 3px;
  background: #5f34ff;
  -webkit-box-shadow: 0 0 2px 2px rgba(112, 115, 148, 0.4);
  -moz-box-shadow: 0 0 2px 2px rgba(112, 115, 148, 0.4);
  box-shadow: 0 0 2px 2px rgba(112, 115, 148, 0.4);
  -webkit-transition: background ease-in-out 0.4s;
  -moz-transition: background ease-in-out 0.4s;
  transition: background ease-in-out 0.4s;
}
.vui-checkbox-warp .vui-checkbox:after {
  content: ' ';
  position: absolute;
  left: 2px;
  top: 2px;
  z-index: 2;
  width: 12px;
  height: 12px;
  border-radius: 2px;
  background: #e6e6e6;
  -webkit-box-shadow: 0 0 2px 2px rgba(112, 115, 148, 0.4);
  -moz-box-shadow: 0 0 2px 2px rgba(112, 115, 148, 0.4);
