}
.vui-checkbox-warp [type=checkbox]:checked ~ .vui-checkbox:after {
  /*加勾*/
  content: '\2714';
  color: #fff;
  line-height: 6px;
  font-size: 18px;
  background: #5f34ff;
  -webkit-box-shadow: 0 0 2px 2px rgba(112, 115, 148, 0);
