
.vui-loading-inner {
  position: relative;
  width: 300px;
  height: 100px;
  top: 50%;
  margin: -50px auto;
  text-align: center;
}
.vui-loading-inner p {
  color: #fff;
  margin-bottom: 10px;
}
.vui-loading-inner span {
  display: inline-block;
  vertical-align: middle;
  width: 10px;
  height: 10px;
  margin: 3px;
  background: #007DB6;
  border-radius: 10px;
  -webkit-animation: loading 1s infinite alternate;
          animation: loading 1s infinite alternate;
  /*&:nth-of-type(7) {*/
  /*background: #A0B61E;*/
  /*animation-delay: 1.2s;*/
  /*}*/
}
.vui-loading-inner span:nth-of-type(2) {
  background: #008FB2;
  -webkit-animation-delay: 0.2s;
          animation-delay: 0.2s;
}
.vui-loading-inner span:nth-of-type(3) {
  background: #009B9E;
  -webkit-animation-delay: 0.4s;
          animation-delay: 0.4s;
}
.vui-loading-inner span:nth-of-type(4) {
  background: #00A77D;
  -webkit-animation-delay: 0.6s;
          animation-delay: 0.6s;
}
.vui-loading-inner span:nth-of-type(5) {
  background: #00B247;
  -webkit-animation-delay: 0.8s;
          animation-delay: 0.8s;
}
.vui-loading-inner span:nth-of-type(6) {
  background: #5AB027;
  -webkit-animation-delay: 1.0s;
          animation-delay: 1.0s;
}
@-webkit-keyframes loading {
0% {
    opacity: 0;
}
100% {
    opacity: 1;
}
}
@keyframes loading {
0% {
    opacity: 0;
}
100% {
    opacity: 1;
}
}



