
body {
  font: 12/1.5 "Helvetica Neue", Helvetica, "PingFang SC", "Hiragino Sans GB", "Microsoft YaHei", "微软雅黑", Arial, sans-serif;
  color: #333;
}
.warper {
  width: 100%;
  min-width: 80%;
}
.warper-content {
  width: 80%;
  margin: 0 auto;
  margin-top: 20px;
}
.content {
  margin: 0 auto;
  position: relative;
}
.menu {
  float: left;
  background: #f2f2f2;
  width: 280px;
  margin-right: 20px;
}
.menu p {
  text-decoration: none;
  cursor: pointer;
}
.main {
  padding: 20px 0 20px 300px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  width: 100%;
  min-height: 300px;
}
.clear {
  zoom: 1;
}
.clear:before,
.clear:after {
  display: table;
  content: "";
}
.clear:after {
  clear: both;
}
.menu-list {
  /* height: 30px;
    line-height: 30px; */
  font-size: 14px;
  text-indent: 1em;
}
.menu-list p:hover > a {
  text-decoration: underline;
}
.menu-active {
  background: #5f34ff;
  color: #fff;
}



